body {
  background: black;
  color: white;
}

.loading-ss {
  position: absolute;
  background: #000;
  top: 0;
  left: 0;
  z-index: 10000000000000;
  margin: 0;
}

.loading-ss .ss-loading {
  display: inline-block;
  position: absolute;
  margin: auto;
  text-align: center;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.loading-ss .ss-loading .ss-container {
  width: 200px;
  height: 100px;
  overflow: hidden;
}

.loading-ss .ss-loading .ss-circle {
  position: absolute;
  width: 100%;
  height: 200%;
  border-radius: 50%;
  overflow: hidden;
}

.loading-ss .ss-loading .ss-top {
  -webkit-transform-origin: 50% 100%;
  transform-origin: 50% 100%;
}

.loading-ss .ss-loading .ss-top .ss-circle {
  box-shadow: inset 0 0 0 10px #41c91f;
}

.loading-ss .ss-loading .ss-bottom {
  -webkit-transform-origin: 50% 0;
  transform-origin: 50% 0;
}

.loading-ss .ss-loading .ss-bottom .ss-circle {
  box-shadow: inset 0 0 0 10px #01b1d1;
  top: -100px !important;
}

.loading-ss .ss-loading .ss-large .ss-container {
  width: 96px;
  height: 48px;
  margin-left: -48px;
}

.loading-ss .ss-loading .ss-large .ss-top .ss-circle {
  box-shadow: inset 0 0 0 6px #41c91f;
}

.loading-ss .ss-loading .ss-large .ss-bottom .ss-circle {
  box-shadow: inset 0 0 0 6px #01b1d1;
  top: -48px !important;
}

.loading-ss .ss-loading .ss-small .ss-container {
  width: 24px;
  height: 12px;
  margin-left: -12px;
}

.loading-ss .ss-loading .ss-small .ss-top .ss-circle {
  box-shadow: inset 0 0 0 2px #41c91f;
}

.loading-ss .ss-loading .ss-small .ss-bottom .ss-circle {
  box-shadow: inset 0 0 0 2px #01b1d1;
  top: -12px !important;
}

.loading-ss .ss-medium .ss-container {
  width: 48px;
  height: 24px;
  margin-left: -12px;
}

.loading-ss .ss-medium .ss-top .ss-circle {
  box-shadow: inset 0 0 0 3px #41c91f;
}

.loading-ss .ss-medium .ss-bottom .ss-circle {
  box-shadow: inset 0 0 0 3px #01b1d1;
  top: -24px !important;
}

.loading-ss .ss-loading .ss-bottom,
.loading-ss .ss-loading .ss-top {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-animation: 0.8s linear infinite ssrotate;
  animation: 0.8s linear infinite ssrotate;
}

@-webkit-keyframes ssrotate {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes ssrotate {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
